*{
    padding: 0;
    margin: 0;
}


.loginPage{
    background: url("../../images/loginBackground.jpg") no-repeat center center/cover;
    height: 100vh;
    width: 100%;
}

.Login-form{
    display: flex;
    flex-direction: column;
    opacity: 0.9;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    background-color: none;
    top: 200px;
    text-align: center;
    background-color: white;
    height: 300px;
    width: 250px;
    border-radius: 20px;
    box-shadow: 1px 1px black;
}

.Login-form form{
    background-color: white;
    display: flex;
    flex-direction: column;
}

.form-row input{
    margin: 4px;
    padding: 5px;
    border: 1px solid black;
    border-radius: 20px;
}

.btn{
    border-radius: 20px;
    background-color: #b24141;
    color: white;
}
.btn:hover{
    border:1px solid black;
    background-color: #c72626;
    color: white;
}

@media(max-width:987px){
    .loginPage{
        background: url("../../images/loginBackgroundPhone.jpg") no-repeat center center/cover
    }
}

.loginChoice{
    display: flex;
}
.form-check{
    margin: 5px;
}