*{
    box-sizing: border-box;
}
@media (min-width: 500px) {
    .carImg{
        height: 650px;
    }
}
@media (max-width: 500px) {
    .quotes{
        font-size: 1rem;
    }
}
.image-fluid{
    width:100%;
    height: auto;
}

.products{
    border: none;
    border-radius: 20px;
    box-sizing: 2px 2px black;
    text-align: center;
    margin: 10px 0px;
}

.heading{
    text-align: center;
}

.heading h1{
    font-size: 2.5rem;
    color: white;
    background-color: #b24141;
}

.content{
    background-color: #ececec;
    text-align: center;
}
.prod-row{
    margin: 10px;
}

.tags{
    font-size: 1.5rem;
}

@media (min-width:700px) {
    .hover-zoom {
        overflow: hidden;
        transition: transform 1s;
      }
      
      .hover-zoom img {
        transition: transform 1s;
      }
      
      .hover-zoom:hover {
        transform: scale(1.1);
      }
      
      .hover-zoom:hover img {
        transform: scale(1.1);
      }
}

@media (max-width:690px){
    .heading h1{
        font-size: 1rem;
    }
    .tags{
        font-size: 0.8rem;
    }
    .btnClass{
        font-size: 0.7rem;
    }
}

.btnClass{
    background-color: #b24141;
    color: white;
    border: none;
    border-radius: 25px;
    margin: 10px;
    padding: 10px;
}

.btnClass:hover{
    background-color: #d12121;
    border: 1px solid white;
}
.row{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background-color: #ececec;
}
.card{
    margin: 10px;
}

@media (max-width:802px) {
    .card{
        margin: 10px;
    }
    .custom{
        font-size: 1rem;
    }
}

.brand-logo{
    padding: 10px;
}
.footer-row{
    color: black;
    background: #b24141;
}

.logo-img{
    height: 50px;
    text-align: center;
}

.col1{
    text-align: center;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.col1 form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input-box{
    padding: 7px;
    margin: 3px;
    border: none;
    border-radius: 20px;
}

.buttonClass{
    padding: 10px;
    border: none;
    border-radius: 20px;
}

.col2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.icons i{
    margin: 3px;
}

.icons a{
    text-decoration: none;
    color: white;
}
.rights{
    text-align: center;
    background-color: #b24141;
    color: white;
}

.col1 h4{
    color: white;
}
.col2 h3{
    color: white;
}

@media (max-width:370px){
    .footer-row{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}